@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;400;500;700&display=swap);
:root {
  --font-family: 'Poppins', sans-serif;

  --gradient-text: linear-gradient(300deg, rgba(54, 54, 54, 1) 0%, rgb(73, 73, 73) 50%, rgba(20, 20, 20, 1) 100%);
  --gradient-orange: linear-gradient(90deg, rgba(220, 141, 5, 1) 1%, rgba(216, 54, 5, 1) 100%);
  
  --color-bg: rgba(236, 238, 242, 0.5);
  --color-box: radial-gradient(54.75% 55.14% at 43.28% 42.57%, rgba(127, 145, 162, 0.075) 55.82%, rgba(215, 219, 227, 0.075) 100%), rgba(236, 238, 242, 0.1);
  --color-text: rgba(52, 52, 52, 0.5);
  --color-mobile: rgba(236, 238, 242, 1);
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  background: var(--color-bg);
  display: "flex";
  justify-content: "center";
  align-items: "center";
}

a {
  color: unset;
  text-decoration: none;
}

.gradient__text {
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.gradient__orange {
  background: var(--gradient-orange);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.section__padding {
  padding: 4rem 6rem;
}

.section__margin {
  margin: 4rem 6rem;
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@media screen and (max-width: 700px) {
  .section__padding {
    padding: 4rem;
  }
  .section__margin {
    margin: 4rem;
  }
}

@media screen and (max-width: 450px) {
  .section__padding {
    padding: 4rem 2rem;
  }
  .section__margin {
    margin: 4rem 2rem;
  }
}
.prof__about {
    max-width: 688px;
    max-height: 410;
    display: flex;
    flex-direction: column;
