    line-height: 72px;
}

.prof__about p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    letter-spacing: 0.03em;
    color: rgba(52, 52, 52, 0.5);
    margin-top: 1.5rem;
}

@media screen and (max-width: 1050px) {
