.prof__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;
    grid-gap: 27px;
    gap: 27px;
}


.prof__form h1 {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 48px;
    line-height: 72px;
}

.prof__form-name input, .prof__form-email input, 
.prof__form-message textarea {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 8px 10px;
    grid-gap: 10px;
    gap: 10px;

    width: 415px;
    height: 46px;
    border: 2px solid #3D3D3D;
    border-radius: 18px;
    background: none;
}

.prof__form-message textarea {
    height: 117px;
}

.prof__form-name label, input,  .prof__form-email label, input,
.prof__form-message label, textarea {
    width: 61px;
    height: 30px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #363636;
}

.prof__form-message textarea::-webkit-scrollbar {
    display: none;
  }

.prof__form-button {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    padding: 6px 35px;
    grid-gap: 10px;
    gap: 10px;

    background: linear-gradient(180deg, #2D2D2D 0%, #1E1E1E 100%);
    box-shadow: 1px 4px 36px rgba(77, 77, 77, 0.5);
    border-radius: 18px;

    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    letter-spacing: 0.03em;
    color: #E5E5E5;
    margin: 2rem auto;
    outline: none;
    cursor: pointer;
    border: none;
}

@media screen and (max-width: 1050px) {
    .prof__form h1 { 
        font-size: 36px;
        line-height: 60px;
    }
    
}

@media screen and (max-width: 650px) {
    .prof__form h1 { 
        font-size: 32px;
        line-height: 56px;
    }
}

@media screen and (max-width: 450px) {

    .prof__form h1 { 
        font-size: 28px;
        line-height: 50px;
    }

    .prof__form-name input, .prof__form-email input, 
    .prof__form-message textarea {
        width: 330px;
    }

}

@media screen and (max-width: 351px) {
    .prof__form-name input, .prof__form-email input, 
    .prof__form-message textarea {
        width: 250px;
    }

}
.prof__navbar {
    width:100%;
    max-width: 1426px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 2rem;
    margin: 0 auto;
}

.prof__navbar-links {
    flex: 1 1;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0px;
    grid-gap: 218px;
    gap: 218px;
    
}

.prof__navbar-links_logo {
    margin-right: 2rem;
    
}

.prof__navbar-links_logo img {
    width: 33px;
    height: 48px;
}

.prof__navbar-links_container {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: left;
    padding: 0px;
    grid-gap: 100px;
    gap: 100px;
}

.prof__navbar-links_container p,
.prof__navbar-menu_container p {
    color: #202129;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.prof__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.prof__navbar-menu svg {
    cursor: pointer;
}

.prof__navbar-menu_container {
    display: flex;
    justify-items: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-mobile);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}

.prof__navbar-menu_container p {
    margin: 1rem 0;
}

@media screen and (max-width: 1050px) {
    .prof__navbar-links_container {
        display: none;
    }
    .prof__navbar-menu {
        display: flex;
    }

    .prof__navbar {
        justify-content: space-between;
    }

}

@media screen and (max-width: 700px) {
    .prof__navbar {
        padding: 2rem 4rem;
    }

    .prof__navbar {
        justify-content: space-between;
    }
}

@media screen and (max-width: 550px) {
    .prof__navbar {
        padding: 2rem;
    }
    .prof__navbar-menu_container {
        top: 20px;
    }

    .prof__navbar {
        justify-content: space-between;
    }
}
